        <img src="../assets/Untitled.png" alt="" class="slika">
      </div>

      <div id="glavna-info">
        <h1>{{ pozdrav }}<span v-if="kursor" id="kursor">|</span></h1>
        <hr>
        <h2>Ako su vam potrebne usluge:</h2>
        <div class="info">
          <router-link to="usluge" id="info-prvi">Izrade sajtova</router-link>
          <router-link to="usluge" id="info-drugi">Časova programiranja</router-link>
          <router-link to="usluge" id="info-treci">Računarske obuke</router-link>
          <router-link to="usluge" id="info-cetvrti">ili nudite zaposlenje</router-link>
        </div>
      </div>
      <router-link to="/kontakt" id="glavna-kontakt">KONTAKTIRAJTE ME</router-link>
