<script>
// @ is an alias to /src
// import HelloWorld from '@/components/HelloWorld.vue'

export default {
  name: 'Home',
  data: function() {
    return {
      pozdrav: "",
      kursor: true
    }
  },
  components: {
    // HelloWorld
  },
  methods: {
    startInterval: function () {
      let poruka = "Zdravo,     ja sam Marko";
      let niz = poruka.split("");
      let i = 0;
      let interval = setInterval(() => {
          this.pozdrav += niz[i++];
          if (i == poruka.length) {
            clearInterval(interval)
            let self = this;
            setTimeout(function(){
              self.kursor = false;
            },1000)
          }
      }, 80);
    }
  },
  created: function () {
    this.startInterval();
  }
}
</script>


