<template>
<main>
  <h1>Kontakt</h1>
  <form v-on:submit="proveraForme" action="https://formspree.io/myyprgeq" method="POST">
    <div v-if="greske.length">
        <ul>
            <li v-for="(greska,index) in greske" v-bind:key="index">{{greska}}</li>
        </ul>
    </div>
    <div>
        <label for="ime">Unesite Vaše ime:</label>
        <input type="text" name="ime" v-model="ime">
    </div>
    <div>
        <label for="email">Unesite Vašu e-mail adresu:</label>
        <input type="text" name="email_adresa" v-model="email">
    </div>
    <div>
        <label for="poruka">Unesite Vašu poruku:</label>
        <textarea name="poruka" v-model="poruka"></textarea>
    </div>
    
    <input type="submit" value="Pošalji">
  </form>
</main>
  
</template>

<script>
export default {
    name: "Contact",
    data: function(){
        return {
            greske: [],
            ime: "",
            email: "",
            poruka: ""
        }
    },
    methods: {
        proveraForme: function(e){
            this.greske = [];
            if (this.ime === "") {
                this.greske.push("Ime je obavezno za unos");
                e.preventDefault();
            } else if (this.ime.length > 15) {
                this.greske.push("Ime ne moze sadrzati vise od 15 karaktera");
                e.preventDefault();
            }

            if (this.email === "") {
                this.greske.push("Email je obavezan za unos");
                 e.preventDefault();
            }
            else if (!this.pravilanEmail(this.email)) {
                this.greske.push("Email adresa nije pravilno uneta");
                 e.preventDefault();
            }

            if (this.poruka === "") {
                this.greske.push("Poruka nije uneta")
            }
        },
        pravilanEmail: function (email) {
            var re = /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
            return re.test(email);
        }
    }
}
</script>

<style scoped>
    h1 {
        text-align: center;
    }

    form {
        width: 800px;
        margin: auto;
    }

    label {
        display: block;
        margin-top: 20px;
    }
    textarea {
        width: 100%;
        height: 300px;
        resize: none;
    }
    input {
        width: 100%;
        height: 40px;
       
    }
    textarea, input {
        margin-top: 10px;
    }
    ul {
        list-style-type: none;
        padding: 0px;
        margin: 0px;
    }
    li {

        color: white;
        font-weight: bold;
        background-color: #940000;
        padding: 8px 10px;
        margin: 0px 0px 15px 0px;
    }

    input[type="submit"] {
        height: 60px;
        line-height: 60px;
        font-size: 18px;
        text-transform: uppercase;
    }
    @media only screen and (max-width: 860px) {
        form {
            width: 90%;
        }
    }
</style>