<template>

   <header>
      <router-link to="/">
        <div id="logo">
            <img src="../assets/logo.png" alt="">
            <div id="logo-shadow"></div>
        </div>
      </router-link>
      <input type="checkbox" v-on:click="activeStatus" v-model="dropStatus">
      <img class="click otvori" src="../assets/hamburger.png" alt="">
      <img class="click ponisti" src="../assets/ponisti.png" alt="">

      <nav>
        <ul>
          <router-link to="/" v-on:click="changePage"><li>Početna</li></router-link>
          <router-link to="omeni" v-on:click="changePage"><li>O meni</li></router-link>
          <router-link to="usluge" v-on:click="changePage"><li>Usluge</li></router-link>
          <router-link to="kontakt" v-on:click="changePage"><li>Kontakt</li></router-link>
          <li><a>Blog</a></li>
        </ul>
      </nav>
    </header>

</template>

<script>

    export default {
        name: 'Navigacija',
        data: function() {
            return {
                dropStatus: false
            }
        },
        methods: {
            activeStatus: function(){
                this.dropStatus = true;
            },
            changePage: function(){
                this.dropStatus = false;
            }
        },
        watch:{
            $route (){
                this.dropStatus = false;
        }
} 

    }

</script>

<style type="text/css" scoped>

    header {
        height: 75px;
        padding: 5px;
        -webkit-box-shadow: 0px 3px 37px -2px rgba(0,0,0,0.75);
        -moz-box-shadow: 0px 3px 37px -2px rgba(0,0,0,0.75);
        box-shadow: 0px 3px 37px -2px rgba(0,0,0,0.75);
        position: relative;
      
    }

    header:hover {
        border-bottom-color: white;
        border-bottom: 1px solid #940000;
        -webkit-box-shadow: none;
        -moz-box-shadow: none;
        box-shadow: none;
        transition: all 0.5s;
    }

    header:hover #logo-shadow {
        -webkit-box-shadow: none;
        -moz-box-shadow: none;
        box-shadow: none;
    }


    header:hover nav > ul > a > li  {
        color: black;
    }

    header h1 {
        color: #940000;
        font-size: 40px;
    }
    #logo {
        position: absolute;
        left: 2%;
        width: 80px;
        height: 100px;
        background: #940000;
        text-align: center;
        color: white;
        z-index: 1
    }
    #logo img {
        width: 60%;
        display: block;
        margin: auto;
        margin-top: 40px;
    }
    #logo-shadow {
        z-index: 2;
        position: absolute;
        bottom: 0px;
        width: 100%;
        height: 38px;
        -webkit-box-shadow: 6px 11px 25px 0px rgba(0,0,0,0.75);
        -moz-box-shadow: 6px 11px 25px 0px rgba(0,0,0,0.75);
        box-shadow: 6px 11px 25px 0px rgba(0,0,0,0.75);
    }

    .click {
        display: none;
        position: absolute;
        right: 20px;
        top: 20px;
        width: 50px;
    }
    .ponisti {
        right: 20px;
        top: 20px;
        width: 50px;
        display: none;
    }

    input[type="checkbox"] {
        display: none;
        width: 100%;
        height: 100%;
        z-index: 5;
        opacity: 0;

