        top: 20px;
        width: 40px;
        height: 40px;
        outline: 1px dashed green;
        cursor: pointer;
    }
    input[type="checkbox"]:checked ~ nav{
        margin-left: 2px;
    }

    nav {
        display: block;
        position: absolute;
        right: 5px;
        top: 20px;
        transition: margin-left 0.7s;
    }
    nav a {
        text-decoration: none;
        color: inherit;
    }

    nav > ul {
        list-style-type: none;
        padding: 0px;
        height: 40px;
        overflow: hidden;
    }

    nav > ul:hover {
        cursor: pointer
    }

    nav li {
        display: inline;
        /* border: 2px solid white; */
        padding: 10px;
        padding-bottom: 20px;
        color: gray;
        font-weight: bold;
        transition: padding-bottom 0.5s;
    }
    
    nav li:hover {
        border-bottom: 3px solid #940000;
        cursor: pointer;
        padding-bottom: 5px;
        /* padding-bottom: 1px; */
    }

    .usluge {
        position: relative;

    }
    .usluge:hover + .usluge-drop {
        display: block;

    }

