        .otvori {
            display: block;
        }
        input[type="checkbox"] {
            display: block;
        }
        input[type="checkbox"]:checked ~ .ponisti {
            display: block !important;
        }
        input[type="checkbox"]:checked ~ .otvori {
            display: none !important;
        }
        nav {
         
            width: 100%;
            padding: 0px;
            margin-top: 120px;
            position: static;
            margin-left: 105%;
        }
        header {
            padding-left:0px;
        }
        nav ul{
            width: 100%;
            height: auto;
        }
        nav ul li {
            display: block;
            float: none;
            text-align: center;
            background: #940000;
            height: 100px;
            line-height: 100px;
            font-size: 20px;

            border: 1px solid white;
            padding: 0px;
            color: white;
            letter-spacing: 2px;
            font-weight: normal;
            text-transform: uppercase;
        }
        header:hover nav > ul > a > li  {
            color: white;
        }
    
}
    
</style>